                var provincialTechnologyCenter;
                console.log(this.provincialTechnologyCenter)
                if (this.provincialTechnologyCenter) {
                    provincialTechnologyCenter = 1;
                } else {
                    provincialTechnologyCenter = 0;
                }
                console.log(provincialTechnologyCenter);
                var baseUrl = this.$store.state.url;
                var that = this;
                this.$axios.post('/industrialdeclaration/addDeclaration', that.$qs.stringify({

                    //四级类目
                    declarationType: sessionStorage.getItem('declarationType'),            //基础表
                    isNew: sessionStorage.getItem('isNew'),            //基础表
                    declarationIndustry: sessionStorage.getItem('declarationIndustry'),            //基础表
                    industryTypeDetail: sessionStorage.getItem('industryTypeDetail'),            //基础表

                    //基本信息表
                    //企业名称
                    companyName: this.baseForm.companyName,      //基础表
                    //项目名称L
                    projectName: this.baseForm.projectName,       //基础表
                    //建设内容
                    projectContent: this.baseForm.projectContent,  //基础表
                    //项目所属行业
                    'projectType': this.baseForm.projectType,
                    //行业二级类目
                    'projectTypeDetail': this.baseForm.projectTypeDetail,
                    //城市
                    province: this.baseForm.province,     //基础表
                    //城市
                    city: this.baseForm.city,     //基础表
                    //区县
                    district: this.baseForm.district,    //基础表
                    //相关信息表
                    //高端制造业所属类别
                    'detailEntity.advancedManufacturingType': this.advancedManufacturingType,
                    //总投资(万元)
                    totalInvestment: this.total.totalInvestment,              //基础表
                    'detailEntity.investmentSelfPrepare': this.total.investmentSelfPrepare,
                    'detailEntity.investmentLoans': this.total.investmentLoans,
                    'detailEntity.investmentUsingForex': this.total.investmentUsingForex,
                    //项目实施后预期效益（万元）
                    'detailEntity.newSalesRevenue': this.expectedReturn.newSalesRevenue,
                    'detailEntity.newProfits': this.expectedReturn.newProfits,
                    'detailEntity.newTax': this.expectedReturn.newTax,
                    'detailEntity.newForex': this.expectedReturn.newForex,
                    //专利情况
                    'detailEntity.patentsDeclaration': this.patent.patentsDeclaration,
                    'detailEntity.patentsObtain': this.patent.patentsObtain,
                    //合作单位
                    'detailEntity.partner': this.partner,
                    //项目起止年限
                    startDate: this.subjectYear.startDate.getFullYear(),     //基础表
                    endDate: this.subjectYear.endDate.getFullYear(),         //基础表
                    // 新产品投放市场时间
                    'detailEntity.productLaunchDate': productLaunchDate,
                    //是否属省级企业技术中心
                    'detailEntity.provincialTechnologyCenter': provincialTechnologyCenter

                }))
                    .then((res) => {
                        console.log(res)
                        if (res.data.code == 0) {
                            that.$message.success(res.data.msg);
                            that.$router.push('/iHome/IndusChart')
                        } else {
                            this.$message({
                                message: res.data.msg,
                                type: 'warning'
                            });
                        }
                    })
                    .catch(err => {
                        this.$message.error('网络错误请稍后再试');
                    })
            },
            //所属行业
            handleChange1() {
                this.baseForm.projectType = this.val[0];
                this.baseForm.projectTypeDetail = this.val[1];
            },
            //所属城市
            handleChange(value) {
                var arr = [];
                value.forEach((item, index) => {
                    arr.push(CodeToText[item])
                })
                this.form.city = arr;
                this.baseForm.province = arr[0];
                this.baseForm.city = arr[1];
                this.baseForm.district = arr[2];
                console.log(this.form.city)

            },
            //获取二级分类
            getClass2() {
                console.log(this.value);
                console.log(this.value1);
                var value = this.value - 1;
                var value1 = this.value1 - 1;
                console.log(this.orginData[value])
            },
            //首页选择值
            geto2Value() {
                console.log(this.o2Value)
            }
        },

    }
</script>

<style lang="less">
    .el-form-item__content {
        margin-left: 180px !important;
    }

    .el-form-item__label {
        width: 166px !important;
    }
</style>



